#project-player {
	background: transparent !important;
	height: auto !important;

	.vjs-fullscreen-control.vjs-control.vjs-button, .vjs-progress-control.vjs-control {
		display: none !important;
	}
}

#project-poster {
	left: 50%;
	transform: translateX(-50%);
}

#project-player-bg{
	position: fixed;
	z-index: 1048;
	top: 0;
	left: 0;
	#project-player-container {
		position: fixed;
		#project-player{}
	}
}

#project-player-bg.Project--dimmedbg{
	background: rgba(0,0,0,0.6);
	width: 100%;
    height: 100%;
}

#project-player-container.Project--glassbg{
    background: rgba(255,255,255,0.6);
    box-shadow: 0px 0px 10px 4px #c3c3c3;
}

#project-player-container.Project--centered {
	top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#project-player-container.Project--topleft {
	top: 0;
    left: 0;
}

#project-player-container.Project--topright {
	top: 0;
    right: 0;
}

#project-player-container.Project--bottomleft {
	bottom: 0;
    left: 0;
}

#project-player-container.Project--bottomright {
	bottom: 0;
    right: 0;
}

#project-player-container>a.close-project{
    position: absolute;
    z-index: 1049;
    right: 5px;
    top: 0;
}




#project-text-overlay{
	position: absolute;
    z-index: 1049;
    width: 100%;
    display: none;
}

#project-text-overlay>span{
	padding: 2px 4px;
}

#project-text-overlay.Textoverlay--top{
	top: 0;
}

#project-text-overlay.Textoverlay--middle{
	top: 50%;
	transform: translateY(-50%);
}

#project-text-overlay.Textoverlay--bottom{
	bottom: 0;
}

#project-text-overlay.Textoverlay--left{
	text-align: left;
}

#project-text-overlay.Textoverlay--center{
	text-align: center;
}

#project-text-overlay.Textoverlay--right{
	text-align: right;
}




#project-clicktocall{
	position: absolute;
    z-index: 1049;
    width: 100%;
    display: none;
}

#project-clicktocall>a{
	padding: 2px 4px;
}

#project-clicktocall.Clicktocall--top{
	top: 0;
}

#project-clicktocall.Clicktocall--middle{
	top: 50%;
	transform: translateY(-50%);
}

#project-clicktocall.Clicktocall--bottom{
	bottom: 0;
}

#project-clicktocall.Clicktocall--left{
	text-align: left;
}

#project-clicktocall.Clicktocall--center{
	text-align: center;
}

#project-clicktocall.Clicktocall--right{
	text-align: right;
}




#project-buttonoverlay{
	position: absolute;
    z-index: 1049;
    width: 100%;
    display: none;
}

#project-buttonoverlay>button{
	border: 0 !important;
  -webkit-transition: all .2s ease-in-out;
       -o-transition: all .2s ease-in-out;
          transition: all .2s ease-in-out;
}

#project-buttonoverlay>button:hover,
#project-buttonoverlay>button:focus{
	background: -moz-linear-gradient(top, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#33000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
}


#project-buttonoverlay.Buttonoverlay--top{
	top: 0;
}

#project-buttonoverlay.Buttonoverlay--middle{
	top: 50%;
	transform: translateY(-50%);
}

#project-buttonoverlay.Buttonoverlay--bottom{
	bottom: 0;
}

#project-buttonoverlay.Buttonoverlay--left{
	text-align: left;
}

#project-buttonoverlay.Buttonoverlay--center{
	text-align: center;
}

#project-buttonoverlay.Buttonoverlay--right{
	text-align: right;
}



#project-formoverlay{
	position: absolute;
    z-index: 1049;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    display: none;
}

#project-formoverlay>section.panel{
	width: 65%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 0px 10px 1px #797979;
}

#project-formoverlay>section.panel>a.close-form{
	position: relative;
    z-index: 1049;
    right: 5px;
    float: right;
}

#project-formoverlay #formoverlay-btn:hover,
#project-formoverlay #formoverlay-btn:focus{
	background: -moz-linear-gradient(top, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#33000000', endColorstr='#00000000',GradientType=0 ); /* IE6-9 */
}

#project-player-container>div.loader-3{
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    background: url('../img/vidload.gif') 50% 50% no-repeat transparent;
    overflow: hidden;
}